@import '~@avtopro/style';
@import '~@avtopro/style/mixins/font.less';
@import '~@avtopro/style/mixins/media.less';
@import '~@avtopro/style/mixins/svg-embedded.less';

/* Большой информационный блок с фоном и опциональной иконкой робота */
.pro-banner {
    position: relative;
    padding: unit(25 / @baseFontSize, rem);
    padding-right: 3rem;
    background-color: @skyBlueLighterColor;
    border-radius: @borderRadius;
    margin-bottom: 20px;
    // Для красоты: чтобы в пустом блоке иконка close
    // размещалась по середине высоты
    min-height: unit(5 / @baseFontSize, rem);

    &::before {
        display: block;
        box-sizing: border-box;
        background-repeat: no-repeat;
    }

    &__header {
        .font(20);

        font-weight: 700;

        &:not(:last-child) {
            margin-bottom: 0.5em;
        }
    }

    &__close {
        position: absolute;
        top: unit(15 / @baseFontSize, rem);
        right: unit(15 / @baseFontSize, rem);
        display: block;
        height: unit(@baseIconHeight, px);
        width: unit(@baseIconHeight, px);
        cursor: pointer;
        color: @grayBlueColor;

        &:hover,
        &:focus {
            color: @steelBlueColor;
        }

        svg {
            .svg-embedded();
        }
    }

    & > p {
        margin-bottom: unit(20 / @baseFontSize, rem);

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.pro-banner--lamp,
.pro-banner--santa,
.pro-banner--info,
.pro-banner--error,
.pro-banner--success,
.pro-banner--warning,
.pro-banner--attention,
.pro-banner--question {
    padding: unit(32 / @baseFontSize, rem);
    padding-left: 224px;

    &::before {
        content: '';
        height: 100%;
    }
}

.pro-banner--lamp {
    &::before {
        background-image: url('../icons/robot/robot-lamp.svg');
    }
}

.pro-banner--santa {
    &::before {
        background-image: url('../icons/robot/robot-santa.svg');
    }
}

.pro-banner--attention {
    &::before {
        background-image: url('../icons/robot/robot-attention-big.svg');
    }
}

.pro-banner--info {
    &::before {
        background-image: url('../icons/robot/robot-information-big.svg');
    }
}

.pro-banner--error {
    &::before {
        background-image: url('../icons/robot/robot-error-big.svg');
    }
}

.pro-banner--success {
    &::before {
        background-image: url('../icons/robot/robot-success-big.svg');
    }
}

.pro-banner--warning {
    &::before {
        background-image: url('../icons/robot/robot-warning-big.svg');
    }
}

.pro-banner--question {
    &::before {
        background-image: url('../icons/robot/robot-question.svg');
    }
}

@media (min-width: @screen-xs + 0.001) {
    .pro-banner::before {
        position: absolute;
        left: 0;
        top: 0;
        padding: 1% 1% 0;
        background-size: contain;
        background-position: 0 100%;
        background-origin: content-box;
    }

    .pro-banner--lamp,
    .pro-banner--santa,
    .pro-banner--info,
    .pro-banner--error,
    .pro-banner--success,
    .pro-banner--warning,
    .pro-banner--question,
    .pro-banner--attention {
        padding-left: unit(187 / @baseFontSize, rem);
        padding-right: unit(35 / @baseFontSize, rem);

        &::before {
            padding: unit(15 / @baseFontSize, rem) unit(25 / @baseFontSize, rem)
                0;
            width: unit(187 / @baseFontSize, rem);
        }
    }
}

@media (min-width: @screen-sm + 0.001) {
    .pro-banner--lamp,
    .pro-banner--santa,
    .pro-banner--info,
    .pro-banner--error,
    .pro-banner--success,
    .pro-banner--warning,
    .pro-banner--question,
    .pro-banner--attention {
        padding-left: unit(224 / @baseFontSize, rem);
        padding-right: unit(65 / @baseFontSize, rem);

        &::before {
            padding: unit(15 / @baseFontSize, rem) unit(44 / @baseFontSize, rem)
                0;
            width: unit(224 / @baseFontSize, rem);
        }
    }
}

@media (max-width: @screen-xs) {
    .pro-banner {
        display: flex;
        flex-direction: column;
        padding: unit(20 / @baseFontSize, rem);
        padding-top: unit(15 / @baseFontSize, rem);
        padding-right: 3rem;

        &::before {
            width: 100%;
            height: unit(80 / @baseFontSize, rem);
            margin: unit(10 / @baseFontSize, rem) 0;
            background-size: contain;
            background-position: 50%;
        }

        &__header {
            .font(14);

            order: -1;
            margin-bottom: 0;
            font-weight: 700;
            text-align: center;

            /* Чтобы текст не наползал на иконку close, приходится добавлять поля */
            padding: 0 unit(20 / @baseFontSize, rem);

            &:not(:last-child) {
                margin-bottom: 0;
            }
        }
    }
}

@theme: default;